.horizontal-chart {
  position: absolute;
  left: 0;
  top: 0;
  height: 32px;
  width: 100%;
  background: #fdfdfd;
  margin: 0;
  overflow: hidden;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.07), 0 2px 6px rgba(0, 0, 0, 0.07);
  transition: box-shadow 400ms cubic-bezier(0.4, 0, 0.2, 1); }
  .horizontal-chart .chart-bar {
    height: 100%;
    position: absolute;
    width: 100%; }
  .horizontal-chart .chart-bar.extend {
    background: #3a3a3a;
    z-index: 2;
    transition: width 400ms cubic-bezier(0.4, 0, 0.2, 1); }

.horizontal-chart.full {
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.13), 0 2px 6px rgba(0, 0, 0, 0.1); }

.vertical-chart {
  cursor: pointer;
  display: inline-block;
  height: 500px;
  width: 100%;
  background: #f5f5f5;
  margin: 0;
  overflow: hidden;
  box-shadow: none;
  transition: box-shadow 400ms cubic-bezier(0.4, 0, 0.2, 1); }
  .vertical-chart .chart-bar {
    height: 100%;
    position: absolute;
    width: 100%;
    bottom: 0; }
  .vertical-chart .chart-bar.extend {
    background: #3a3a3a;
    z-index: 2;
    transition: height 400ms cubic-bezier(0.4, 0, 0.2, 1); }
  .vertical-chart .chart-container {
    width: 100%;
    height: 100%;
    position: relative; }
    .vertical-chart .chart-container .chart-day-container {
      position: absolute;
      z-index: 5;
      color: #3a3a3a;
      left: 50%;
      width: 100%;
      transform: translateX(-50%);
      padding: 8px;
      line-height: 100%;
      text-align: left;
      transition: all 400ms cubic-bezier(0.4, 0, 0.2, 1); }
      .vertical-chart .chart-container .chart-day-container .chart-day-name {
        display: block;
        font-size: 28px;
        font-weight: bolder;
        line-height: 110%; }
      .vertical-chart .chart-container .chart-day-container .chart-day-num {
        display: block;
        font-size: 20px;
        font-weight: lighter; }
      .vertical-chart .chart-container .chart-day-container .chart-day-percent {
        display: inline-block;
        font-weight: bolder;
        font-size: 58px;
        line-height: 100%;
        margin-right: 8px; }
      .vertical-chart .chart-container .chart-day-container .dayinfo-container {
        display: inline-block; }
