main#dashboard-container {
  margin-top: 1px;
  text-align: center;
  position: relative;
  width: 100%;
  height: calc(100% - 50px);
  overflow-x: hidden; }

.widget {
  position: absolute;
  top: 0;
  left: 0;
  transition: left 1000ms cubic-bezier(0.4, 0, 0.2, 1);
  flex-shrink: 1;
  flex-grow: 0;
  width: 100%; }

.footer {
  position: relative;
  bottom: 0; }
