.goal {
  background: #fdfdfd;
  position: relative;
  height: 135px;
  width: calc(25% - 32px);
  min-width: 175px;
  margin: 0 16px;
  margin-bottom: 32px;
  color: #3a3a3a;
  text-align: center;
  font-size: 18px;
  cursor: pointer;
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.07), 0 2px 6px rgba(0, 0, 0, 0.07);
  transition: all 100ms cubic-bezier(0.4, 0, 0.2, 1); }
  .goal .goal-body {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.goal.done {
  background: #3a3a3a;
  color: #fdfdfd; }

.goal:hover {
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.13), 0 2px 6px rgba(0, 0, 0, 0.1); }

.goal.button {
  background: none;
  color: #e4e4e4;
  font-size: 20px;
  box-shadow: none;
  border: 4px dashed #e4e4e4;
  font-weight: normal; }
  .goal.button .goal-plus {
    font-size: 112px;
    line-height: 100%;
    font-weight: lighter; }

.goal.button:hover {
  border-color: #3a3a3a;
  color: #3a3a3a; }
